.navbar {
    background-color: #fff;
    position: sticky;
    top: 0;
    z-index: 11;
    height: 10vh;
    margin-bottom: 5px;
    box-shadow: 10px 10px 15px -6px rgba(0,0,0,0.18);
}
.navbar ul {
    align-items: center;
}
.navbar ul li {
    display: inline-block;
    transition: all 0.5s ease-in;
    margin: 0 25px 0 0;
}
.navbar ul li .isActive {
    color:#00d690;
}
.navbar ul li a {
    color: #283a5e;
    font-weight: 100;
    font-style: 15px;
    letter-spacing: 2px;
    transition: all .5s ease-in-out;
    opacity: .8;
}
.navbar ul li a:hover {
    opacity: .3;
}
.nav-links-mobile {
    display: none;
}
.menu-icon {
    display: none;
}
nav svg {
    color: #283a5e;
}
.login-area li {
    margin: 10px 20px 0 0;
}
.login-area a {
    font-weight: 100;
    opacity: 0.8;
    font-size: 15px;
    color: #fff;
}
.login-area svg {
    margin: 0 5px 0 15px;
    font-size: 13px;
    color: #283a5e;
}
.login-area a button {
    margin-left: 20px;
}

@media screen and (max-width : 768px) {
    .login-area {
        position: absolute;
        right: 20px;
    }
    .navbar {
        text-align: center;
    }
   .navbar ul li a {
    color: #fff;
   } 
    .navbar ul.nav-menu {
        display: flex;
        width: 100%;
        height: 45vh;
        position: absolute;
        flex-direction: column;
        justify-content: space-evenly;
        top: 80px;
        left: -105%;
        opacity: 1;
        transition: all .5s ease;
    }
    .navbar ul li {
        display: block;
        width: 99%;
    }
    .navbar ul.active {
        background-color: #31b675;
        left: 0;
        top: 100%;
        opacity: 1;
        transition: 0.5s;
        z-index: 100;
    }
    .navbar ul li a {
        font-weight: 400;
    }
    .menu-icon {
        display: block;
        position: absolute;
        left: 20px;
        font-size: 1.8rem;
        cursor: pointer;
    }
    .faCircleXmark {
        font-size: 2rem;
    }
    .login-area .primary-btn {
        display: none;
    }
}